/* global Quagga */

let isRunning = false;
let quaggaInitialized = false;

function setStatus(msg, type = '') {
  const statusContainer = document.getElementById('status');
  if (statusContainer) {
    statusContainer.textContent = msg;
    statusContainer.className = type ? `status ${type}` : 'status';
  }
}

function setResult(msg, type = '') {
  const resultContainer = document.getElementById('result');
  if (resultContainer) {
    resultContainer.textContent = msg;
    resultContainer.className = type ? `result ${type}` : 'result';
  }
}

function populateDeviceSelect() {
  const deviceSelect = document.getElementById('deviceSelect');
  if (!deviceSelect) return;

  navigator.mediaDevices.enumerateDevices()
    .then(devices => {
      const videoDevices = devices.filter(device => device.kind === 'videoinput');
      deviceSelect.innerHTML = '';
      videoDevices.forEach(device => {
        const option = document.createElement('option');
        option.value = device.deviceId;
        option.text = device.label || `Camera ${deviceSelect.length + 1}`;
        deviceSelect.appendChild(option);
      });
      
      if (videoDevices.length === 0) {
        const option = document.createElement('option');
        option.text = 'No cameras found';
        deviceSelect.appendChild(option);
        deviceSelect.disabled = true;
      }
    })
    .catch(err => {
      console.error('Error enumerating devices:', err);
      const option = document.createElement('option');
      option.text = 'Error loading cameras';
      deviceSelect.innerHTML = '';
      deviceSelect.appendChild(option);
      deviceSelect.disabled = true;
    });
}

async function startQuagga() {
  if (isRunning || quaggaInitialized) return;
  
  try {
    isRunning = true;
    quaggaInitialized = true;
    setStatus('Starting camera...', 'stripe');
    setResult('No barcode detected');

    const formatSelect = document.getElementById('formatSelect');
    const deviceSelect = document.getElementById('deviceSelect');
    
    // Request camera permission first
    await navigator.mediaDevices.getUserMedia({ video: true });
    
    const config = {
      inputStream: {
        name: "Live",
        type: "LiveStream",
        target: document.querySelector('#interactive'),
        constraints: {
          deviceId: deviceSelect && deviceSelect.value ? deviceSelect.value : undefined,
          facingMode: deviceSelect && deviceSelect.value ? undefined : "environment",
          aspectRatio: { min: 1, max: 2 },
          frameRate: { max: 30 }
        },
        area: { // Only search for barcodes in the center of the image
          top: "0%",
          right: "0%",
          left: "0%",
          bottom: "0%"
        },
        singleChannel: false
      },
      decoder: {
        readers: [formatSelect ? formatSelect.value : 'ean_13'],
        multiple: false
      },
      locate: true,
      locator: {
        halfSample: true,
        patchSize: "medium"
      },
      numOfWorkers: 4,
      frequency: 10,
      debug: false
    };

    Quagga.init(config, function(err) {
      if (err) {
        console.error('Camera initialization error:', err);
        if (err.name === 'NotAllowedError') {
          setStatus('Camera access denied. Please grant camera permissions.', 'error');
        } else if (err.name === 'NotFoundError') {
          setStatus('No camera found on this device.', 'error');
        } else if (err.name === 'NotReadableError') {
          setStatus('Camera is in use by another application.', 'error');
        } else {
          setStatus('Error initializing camera: ' + err.message, 'error');
        }
        setResult('Initialization failed', 'error');
        isRunning = false;
        quaggaInitialized = false;
        return;
      }

      Quagga.start();
      setStatus('Camera started. Aim at a barcode.', 'success');
    });

    Quagga.onDetected(function(result) {
      if (result.codeResult && result.codeResult.code) {
        setResult(`Barcode Detected: ${result.codeResult.code} (Format: ${result.codeResult.format})`, 'success');
        setStatus('Barcode detected!', 'success');
        stopQuagga();
      }
    });

    Quagga.onProcessed(function(result) {
      const drawingCtx = Quagga.canvas.ctx.overlay;
      const drawingCanvas = Quagga.canvas.dom.overlay;

      if (result) {
        if (result.boxes) {
          drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
          result.boxes.filter(function(box) {
            return box !== result.box;
          }).forEach(function(box) {
            Quagga.ImageDebug.drawPath(box, { x: 0, y: 1 }, drawingCtx, { color: "green", lineWidth: 2 });
          });
        }

        if (result.box) {
          Quagga.ImageDebug.drawPath(result.box, { x: 0, y: 1 }, drawingCtx, { color: "#00F", lineWidth: 2 });
        }

        if (result.codeResult && result.codeResult.code) {
          Quagga.ImageDebug.drawPath(result.line, { x: 'x', y: 'y' }, drawingCtx, { color: 'red', lineWidth: 3 });
        }
      }
    });
  } catch (error) {
    console.error('Error starting camera:', error);
    setStatus('Error starting camera: ' + error.message, 'error');
    isRunning = false;
    quaggaInitialized = false;
  }
}

function stopQuagga() {
  if (!isRunning) return;
  
  // Remove event listeners
  Quagga.offDetected();
  Quagga.offProcessed();
  
  // Stop the video stream
  const videoElement = document.querySelector('#interactive video');
  if (videoElement && videoElement.srcObject) {
    const tracks = videoElement.srcObject.getTracks();
    tracks.forEach(track => track.stop());
    videoElement.srcObject = null;
  }
  
  // Stop Quagga
  Quagga.stop();
  
  // Clear the viewport
  const viewport = document.querySelector('#interactive');
  if (viewport) {
    const canvases = viewport.querySelectorAll('canvas');
    canvases.forEach(canvas => {
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);
    });
  }
  
  setStatus('Camera stopped.', 'stripe');
  isRunning = false;
  quaggaInitialized = false;
}

function handleVisibilityChange() {
  if (document.visibilityState === 'visible') {
    startQuagga();
  } else {
    stopQuagga();
  }
}

document.addEventListener('visibilitychange', handleVisibilityChange);

document.addEventListener("DOMContentLoaded", function () {
  // Add barcode format selector if not present
  if (!document.getElementById('formatSelect')) {
    const formatSelect = document.createElement('select');
    formatSelect.id = 'formatSelect';
    formatSelect.innerHTML = `
      <option value="ean_13">EAN-13</option>
      <option value="ean_8">EAN-8</option>
      <option value="upc_a">UPC-A</option>
      <option value="upc_e">UPC-E</option>
      <option value="code_128">Code 128</option>
      <option value="code_39">Code 39</option>
      <option value="code_39_vin">Code 39 VIN</option>
      <option value="codabar">Codabar</option>
      <option value="i2of5">Interleaved 2 of 5</option>
      <option value="2of5">Standard 2 of 5</option>
      <option value="code_93">Code 93</option>
    `;
    formatSelect.value = 'ean_13';
    const controls = document.querySelector('.controls');
    const startButton = document.getElementById('startButton');
    if (controls && startButton) {
      controls.insertBefore(formatSelect, startButton);
    }
  }

  // Populate camera devices
  populateDeviceSelect();

  // Add event listeners for camera control buttons
  const startButton = document.getElementById('startButton');
  const stopButton = document.getElementById('stopButton');
  
  if (startButton) {
    startButton.addEventListener('click', () => {
      startQuagga();
      startButton.disabled = true;
      if (stopButton) stopButton.disabled = false;
    });
  }
  
  if (stopButton) {
    stopButton.addEventListener('click', () => {
      stopQuagga();
      stopButton.disabled = true;
      if (startButton) startButton.disabled = false;
    });
  }
});

export { startQuagga, stopQuagga }; 